import { AgGridVue } from "ag-grid-vue3";
import axios from "axios";
import Swal from 'sweetalert2';
export default {
  components: {
    AgGridVue,
    Swal
  },
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      rowData: [],
      searchType: "m.mat_order_code",
      // 검색 조건 
      searchText: "",
      // 검색어
      columnDefs: [{
        field: "mat_order_code",
        headerName: "발주코드",
        flex: 1
      }, {
        field: "mat_order_name",
        headerName: "발주명",
        flex: 1
      }, {
        field: "company_name",
        headerName: "업체명",
        flex: 1
      }, {
        field: "request_date",
        headerName: "발주일자",
        flex: 1,
        valueFormatter: params => {
          // 날짜 형식을 YYYY-MM-DD로 변환
          if (params.value) {
            const date = new Date(params.value);
            return date.toISOString().split('T')[0]; // YYYY-MM-DD 형식으로 변환
          }
          return params.value;
        }
      }, {
        field: "delivery_date",
        headerName: "입고예정일자",
        flex: 1,
        valueFormatter: params => {
          // 날짜 형식을 YYYY-MM-DD로 변환
          if (params.value) {
            const date = new Date(params.value);
            return date.toISOString().split('T')[0]; // YYYY-MM-DD 형식으로 변환
          }
          return params.value;
        }
      }, {
        field: "mat_name",
        headerName: "자재명",
        flex: 2
      }, {
        field: "total_quantity",
        headerName: "발주총갯수",
        flex: 1
      }, {
        field: "status_name",
        headerName: "발주상태",
        flex: 1,
        cellStyle: params => {
          if (params.value == '검수대기') {
            return {
              color: '#0284C7',
              textAlign: 'center',
              fontWeight: 'bold'
            }; // 파란색
          } else if (params.value == '검수완료') {
            return {
              color: '#22C55E',
              textAlign: 'center',
              fontWeight: 'bold'
            }; // 초록색
          } else if (params.value == '반품처리') {
            return {
              color: '#E02D2D',
              textAlign: 'center',
              fontWeight: 'bold'
            }; // 빨간색
          } else if (params.value == '검수중') {
            return {
              color: '#FF7F00',
              textAlign: 'center',
              fontWeight: 'bold'
            }; // 빨간색
          }
          return null; // 기본 스타일
        }
      }],
      gridOptions: {
        domLayout: "autoHeight",
        singleClickEdit: true,
        suppressRowClickSelection: true,
        pagination: true,
        paginationPageSize: 5,
        paginationPageSizeSelector: false,
        overlayNoRowsTemplate: '표시할 값이 없습니다.',
        defaultColDef: {
          suppressMovable: true,
          resizable: false,
          sortable: false,
          cellStyle: {
            textAlign: "center"
          }
        }
      }
    };
  },
  mounted() {
    this.matList();
  },
  methods: {
    close() {
      this.$emit("close");
    },
    matList() {
      axios.get('/api/mat/orderList').then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    searchMaterials() {
      axios.get('/api/mat/orderList', {
        params: {
          type: this.searchType,
          keyword: this.searchText
        }
      }).then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    clicked(event) {
      if (event.data.status_name == '검수완료' || event.data.status_name == '반품처리' || event.data.status_name == '검수중') {
        Swal.fire({
          title: '선택불가',
          text: '이미 검수가 진행된 항목에 대해선 선택이 불가합니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }
      this.$emit('selectOrder', event.data);
      this.close();
    }
  },
  watch: {
    visible(newVal) {
      if (newVal) {
        this.matList(); // visible이 true로 바뀌면 쿼리 실행
      }
    }
  }
};