import { AgGridVue } from "ag-grid-vue3";
import axios from "axios";
export default {
  components: {
    AgGridVue
  },
  props: {
    visible: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      rowData: [],
      searchType: "mat_code",
      // 검색 조건 
      searchText: "",
      // 검색어
      columnDefs: [{
        field: "mat_code",
        headerName: "자재코드",
        flex: 1
      }, {
        field: "mat_name",
        headerName: "자재명",
        flex: 1
      }, {
        field: "unit",
        headerName: "단위",
        flex: 1
      }, {
        field: "by_unit_number",
        headerName: "단위별 갯수",
        flex: 1,
        valueFormatter: params => {
          return params.value != null ? `${params.value}개` : '';
        }
      }, {
        field: "size",
        headerName: "크기",
        flex: 1
      }, {
        field: "safe_inventory",
        headerName: "안전재고",
        flex: 1,
        valueFormatter: params => {
          return params.value != null ? `${params.value}개` : '';
        }
      }],
      gridOptions: {
        domLayout: "autoHeight",
        singleClickEdit: true,
        suppressRowClickSelection: true,
        pagination: true,
        paginationPageSize: 5,
        paginationPageSizeSelector: false,
        overlayNoRowsTemplate: '표시할 값이 없습니다.',
        defaultColDef: {
          suppressMovable: true,
          resizable: false,
          sortable: false,
          cellStyle: {
            textAlign: "center"
          }
        }
      }
    };
  },
  mounted() {
    this.matList();
  },
  methods: {
    close() {
      this.$emit("close");
    },
    matList() {
      axios.get('/api/mat/matList').then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    searchMaterials() {
      axios.get('/api/mat/matList', {
        params: {
          type: this.searchType,
          keyword: this.searchText
        }
      }).then(res => {
        this.rowData = res.data;
      }).catch(error => {
        console.error(error);
      });
    },
    clicked(event) {
      this.$emit('selectMat', event.data);
      this.close();
    }
  }
};