import "core-js/modules/es.array.push.js";
import "core-js/modules/es.iterator.constructor.js";
import "core-js/modules/es.iterator.filter.js";
import "core-js/modules/es.iterator.for-each.js";
import "core-js/modules/es.iterator.map.js";
import { AgGridVue } from "ag-grid-vue3";
import DatePickerEditor from "../../../components/DatePickerEditor.vue";
import axios from "axios";
import MatModal from "@/components/modal/MatModal.vue";
import ComModal from "@/components/modal/ComModal.vue";
import MatOrderModal from "@/components/modal/MatOrderModal.vue";
import Swal from 'sweetalert2';
import { useUserStore } from '@/stores/user';
export default {
  components: {
    AgGridVue,
    datePicker: DatePickerEditor,
    MatModal,
    ComModal,
    MatOrderModal,
    Swal
  },
  data() {
    return {
      //메인그리드
      rowData: [{
        mat_order_code: "",
        mat_order_name: "",
        company_code: "",
        company_name: "",
        request_date: this.getToday(),
        delivery_date: "",
        emp_name: useUserStore().empName,
        emp_code: useUserStore().id,
        note: ""
      }],
      //메인그리드 필드
      columnDefs: [{
        field: "mat_order_code",
        headerName: "자재발주코드",
        flex: 2
      }, {
        field: "mat_order_name",
        headerName: "발주명",
        flex: 2,
        editable: true
      }, {
        field: "company_code",
        headerName: "업체코드",
        flex: 2
      }, {
        field: "company_name",
        headerName: "업체명",
        flex: 2
      }, {
        field: "request_date",
        headerName: "발주일자",
        flex: 2
      }, {
        field: "delivery_date",
        headerName: "납기예정일",
        flex: 3,
        editable: true,
        cellEditor: "datePicker"
      }, {
        field: "emp_name",
        headerName: "사원명",
        flex: 3
      }, {
        field: "emp_code",
        headerName: "등록자",
        flex: 3,
        hide: true
      }, {
        field: "note",
        headerName: "비고",
        flex: 3,
        editable: true
      }],
      //상세그리드
      secondRowData: [{
        mat_order_detailcode: "",
        mat_code: "",
        mat_name: "",
        mat_order_code: '',
        request_quantity: 0
      }],
      //상세그리드 필드
      secondColumnDefs: [
      // 체크박스 추가
      {
        headerCheckboxSelection: true,
        checkboxSelection: true,
        width: 50
      }, {
        field: "mat_order_detailcode",
        headerName: "자재발주상세코드",
        flex: 2
      }, {
        field: "mat_order_code",
        headerName: "자재발주코드",
        flex: 2
      }, {
        field: "mat_code",
        headerName: "자재코드",
        flex: 3
      }, {
        field: "mat_name",
        headerName: "자재명",
        flex: 3
      }, {
        field: "request_quantity",
        headerName: "발주량",
        flex: 2,
        editable: true,
        valueFormatter: params => {
          return params.value != null ? `${params.value}개` : '';
        }
      }],
      gridOptions: {
        domLayout: "autoHeight",
        //행을 보고 자동으로 hight부여
        singleClickEdit: true,
        //한번클릭했을때 수정
        suppressRowClickSelection: true,
        //	행 클릭할 때 체크박스 선택 방지
        defaultColDef: {
          suppressMovable: true,
          //셀 이동 금지
          resizable: false,
          // 열 크기 조정 가능
          sortable: false,
          //정렬 금지
          cellStyle: {
            textAlign: "center"
          },
          headerClass: "header-center"
        }
      },
      showMatModal: false,
      showComModal: false,
      showOrderModal: false,
      //메인그리드 행 인덱스 ary
      selectedRowIndexes: [],
      //상세그리드 행 인덱스
      selectedSecondIndex: null,
      orderExist: false
    };
  },
  mounted() {
    this.autoMatCode();
  },
  methods: {
    // 사이트 접속시 matCode 자동증가
    async autoMatCode() {
      const result = await axios.get("/api/mat/autoMatCode");
      this.rowData[0].mat_order_code = result.data[0].mat_order_code;
      this.secondRowData[0].mat_order_code = result.data[0].mat_order_code;
    },
    //자재코드를 클릭했을때 모달창 열기
    matCellClicked(params) {
      if (params.colDef.field == "mat_code" || params.colDef.field == "mat_name") {
        this.selectedSecondIndex = params.rowIndex;
        this.showMatModal = true;
      }
    },
    //업체코드를 클릭했을때 모달창 열기 comCellClicked
    comCellClicked(params) {
      if (params.colDef.field == "company_code" || params.colDef.field == "company_name") {
        this.selectedSecondIndex = params.rowIndex;
        this.showComModal = true;
      }
    },
    //자재 모달창 값 전달
    matSelected(mat) {
      for (let rowInclude of this.secondRowData) {
        if (rowInclude.mat_code == mat.mat_code) {
          Swal.fire({
            title: '실패',
            text: '같은 자재를 기입할 수 없습니다.',
            icon: 'error',
            confirmButtonText: '확인'
          });
          return;
        }
      }
      this.secondRowData[this.selectedSecondIndex].mat_code = mat.mat_code;
      this.secondRowData[this.selectedSecondIndex].mat_name = mat.mat_name;
      // 새 배열로 설정하여 AG Grid가 반영하게 만듬
      this.secondRowData = [...this.secondRowData];
    },
    //업체 모달창 값 전달
    comSelected(com) {
      this.rowData[0].company_code = com.company_code;
      this.rowData[0].company_name = com.company_name;
      //새 배열로 설정하여 AG Grid가 반영하게 만듬
      this.rowData = [...this.rowData];
    },
    //행추가
    addRow() {
      if (this.orderExist) {
        Swal.fire({
          title: '실패',
          text: '수정중 행추가가 불가합니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }
      this.secondRowData.push({
        mat_order_detailcode: "",
        mat_code: "",
        mat_name: "",
        mat_order_code: this.rowData[0].mat_order_code,
        request_quantity: 0
      });
      //새 배열로 설정하여 AG Grid가 반영하게 만듬
      this.secondRowData = [...this.secondRowData];
    },
    //행삭제
    deleteRow() {
      if (this.orderExist) {
        Swal.fire({
          title: '실패',
          text: '수정중 행삭제가 불가합니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }
      const selectedNodes = this.$refs.secondGrid.api.getSelectedNodes();
      const selectedData = selectedNodes.map(node => node.data);

      // secondRowData에서 선택된 행을 제외한 나머지만 남긴다
      this.secondRowData = this.secondRowData.filter(row => !selectedData.includes(row));
    },
    //현재날짜 가져오기
    getToday() {
      const today = new Date();
      const year = today.getFullYear();
      const month = String(today.getMonth() + 1).padStart(2, '0'); //월은 0부터 시작하니까 +1
      const day = String(today.getDate()).padStart(2, '0');
      return `${year}-${month}-${day}`; //"2025-04-27" 형식
    },
    //값이 다있는지 체크
    fullCheck() {
      //메인그리드 값 다들어 갔는지 체크
      if (this.rowData[0].company_code == '' || this.rowData[0].company_name == '', this.rowData[0].delivery_date == '' || this.rowData[0].request_date == '', this.rowData[0].mat_order_name == '') {
        Swal.fire({
          title: '실패',
          text: '해당하는 값을 입력해주십시오.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return 1;
      } else if (this.rowData[0].delivery_date < this.rowData[0].request_date) {
        Swal.fire({
          title: '실패',
          text: '납기일자가 발주신청일자보다 빠릅니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return 2;
      }
      //상세그리드 값 다들어 갔는지 체크
      for (let rowInclude of this.secondRowData) {
        if (rowInclude.mat_code == '' || rowInclude.mat_name == '', rowInclude.request_quantity == 0) {
          Swal.fire({
            title: '실패',
            text: '값을 다입력하십시오',
            icon: 'error',
            confirmButtonText: '확인'
          });
          return 1;
        }
      }
    },
    //주문등록
    async addOrder() {
      const res = await axios.get('/api/mat/orderCheck', {
        params: {
          orderCode: this.rowData[0].mat_order_code
        }
      }).catch(err => console.log(err));
      if (res.data[0].checkCount > 0) {
        Swal.fire({
          title: '등록 실패',
          text: '이미 등록이 진행된 발주코드입니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }
      if (this.fullCheck() == 1) {
        return;
      } else if (this.fullCheck() == 2) {
        return;
      }
      //등록
      axios.post('/api/mat/matOrderAdd', {
        matOrder: this.rowData[0],
        matOrderDetail: this.secondRowData
      }).then(res => {
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: '등록 성공',
            text: '정상적으로 등록이 완료되었습니다.',
            icon: 'success',
            confirmButtonText: '확인'
          });
          this.orderExist = false;
        } else {
          Swal.fire({
            title: '등록 실패',
            text: '등록이 실패하였습니다..',
            icon: 'error',
            confirmButtonText: '확인'
          });
        }
      }).catch(error => {
        console.error(error);
        Swal.fire({
          title: '등록 실패',
          text: '알수 없는 오류가 발생하였습니다..',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      });
      this.rowData = [{
        mat_order_code: "",
        mat_order_name: "",
        company_code: "",
        company_name: "",
        request_date: this.getToday(),
        delivery_date: "",
        emp_name: useUserStore().empName,
        emp_code: useUserStore().id,
        note: ""
      }];
      this.secondRowData = [{
        mat_order_detailcode: "",
        mat_code: "",
        mat_name: "",
        mat_order_code: '',
        request_quantity: 0
      }];
      this.autoMatCode();
    },
    //주문 조회 모달창 띄우기
    matOrderList() {
      this.showOrderModal = true;
    },
    //발주그리드 값 전달
    async orderSelected(order) {
      //메인그리드로 전달 ajax
      await axios.get('/api/mat/render', {
        params: {
          orderCode: order.mat_order_code
        }
      }).then(res => {
        console.log('받아온 데이터:', res.data);
        const data = res.data;

        // 여기서 날짜 필드 가공
        data.forEach(item => {
          if (item.request_date) {
            // "YYYY-MM-DD"로
            item.request_date = item.request_date.substring(0, 10);
          }
          if (item.delivery_date) {
            // "YYYY-MM-DD"로
            item.delivery_date = item.delivery_date.substring(0, 10);
          }
        });
        this.rowData = data;
      }).catch(err => console.log(err));
      //상세 그리드로 전달 ajax
      await axios.get('/api/mat/detailRender', {
        params: {
          orderCode: order.mat_order_code
        }
      }).then(res => {
        this.orderExist = true;
        this.secondRowData = res.data;
      });
    },
    //수정
    async modifyOrder() {
      const res = await axios.get('/api/mat/orderCheck', {
        params: {
          orderCode: this.rowData[0].mat_order_code
        }
      }).catch(err => console.log(err));
      console.log(res);
      if (res.data[0].checkCount < 1) {
        Swal.fire({
          title: '수정 실패',
          text: '아직 등록되지 않은 건입니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      }
      //값 다 들어가있는지 체크
      if (this.fullCheck() == 1) {
        return;
      } else if (this.fullCheck() == 2) {
        return;
      }
      //수정시작
      await axios.put('/api/mat/modify', {
        matOrder: this.rowData[0],
        matOrderDetail: this.secondRowData
      }).then(res => {
        if (res.data.affectedRows > 0) {
          Swal.fire({
            title: '수정 완료',
            text: '수정이 완료되었습니다.',
            icon: 'success',
            confirmButtonText: '확인'
          });
          this.orderExist = false;
        } else {
          Swal.fire({
            title: '수정 실패',
            text: '수정을 실패하였습니다.',
            icon: 'error',
            confirmButtonText: '확인'
          });
          return;
        }
      }).catch(err => {
        console.error(err);
        Swal.fire({
          title: '수정 실패',
          text: '알수 없는 에러.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return;
      });
      this.rowData = [{
        mat_order_code: "",
        mat_order_name: "",
        company_code: "",
        company_name: "",
        request_date: this.getToday(),
        delivery_date: "",
        emp_name: useUserStore().empName,
        emp_code: useUserStore().id,
        note: ""
      }];
      this.secondRowData = [{
        mat_order_detailcode: "",
        mat_code: "",
        mat_name: "",
        mat_order_code: '',
        request_quantity: 0
      }];
      this.autoMatCode();
    },
    //삭제
    async deleteOrder() {
      await axios.delete('/api/mat/delete/' + this.rowData[0].mat_order_code).then(res => {
        if (res.data.affectedRows < 1) {
          Swal.fire({
            title: '삭제 실패',
            text: '삭제 실패 하였습니다.',
            icon: 'error',
            confirmButtonText: '확인'
          });
          this.orderExist = false;
        } else {
          Swal.fire({
            title: '삭제 완료',
            text: '정상적으로 삭제가 완료되었습니다.',
            icon: 'success',
            confirmButtonText: '확인'
          });
        }
      }).catch(err => console.log(err));
      this.rowData = [{
        mat_order_code: "",
        mat_order_name: "",
        company_code: "",
        company_name: "",
        request_date: this.getToday(),
        delivery_date: "",
        emp_name: useUserStore().empName,
        emp_code: useUserStore().id,
        note: ""
      }];
      this.secondRowData = [{
        mat_order_detailcode: "",
        mat_code: "",
        mat_name: "",
        mat_order_code: '',
        request_quantity: 0
      }];
      this.autoMatCode();
    },
    async pdfDown() {
      // 발주 목록이 없는 경우 다운로드 불가 메시지 표시
      if (!this.orderExist) {
        Swal.fire({
          title: '실패',
          text: '발주 목록은 조회후 다운로드가 가능합니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
        return; // 발주 목록이 없으면 다운로드를 중지하고 함수 종료
      }
      try {
        // 서버에 POST 요청을 보내 PDF 파일을 요청
        const response = await axios.post('/api/mat/pdfDownload', {
          rowData: this.rowData,
          // 발주 데이터
          rowDataDetail: this.secondRowData // 발주 세부 데이터
        }, {
          responseType: 'blob' // 파일을 blob 형태로 응답받기
        });

        // Blob 형태로 받은 데이터를 Object URL로 변환
        const url = window.URL.createObjectURL(new Blob([response.data]));

        // 다운로드를 위한 <a> 태그 동적으로 생성
        const a = document.createElement('a');
        a.href = url; // Blob URL을 <a> 태그의 href에 설정
        a.download = '발주서.pdf'; // 다운로드할 파일 이름 설정
        a.click(); // 자동으로 <a> 태그를 클릭하여 다운로드 시작

        // 다운로드 후 Object URL 해제
        window.URL.revokeObjectURL(url); // 메모리에서 Blob URL을 해제하여 메모리 누수 방지
      } catch (err) {
        // 오류가 발생한 경우 콘솔에 오류를 출력하고 사용자에게 알림

        Swal.fire({
          title: '다운로드 실패',
          text: 'PDF 다운로드 중 오류가 발생했습니다.',
          icon: 'error',
          confirmButtonText: '확인'
        });
      }
    }
  }
};